<template>
  <div class="homepage clearfix">
    <div class="contentLeft">
      <div class="contentTitle"><h1>欢迎您，{{ name }}</h1></div>
      <div class="contentList">
        <ul class="clearfix">
          <li @click="newProject()">
            <router-link to="">
              <span class="iconfont el-icon-emergency-xinjian"></span>
              <p>新建演练项目</p>
            </router-link>
          </li>
          <li @click="sureJoin()">
             <router-link to="">
              <span class="iconfont el-icon-emergency-canjia"></span>
              <p>可参加的演练</p>
            </router-link>
          </li>
          <li @click="toSee()">
            <router-link to="">
              <span class="iconfont el-icon-emergency-changjingyanlian"></span>
              <p>可观看的演练</p>
            </router-link>
          </li>
          <li @click="toHistory(2)">
            <router-link to="">
              <span class="iconfont el-icon-emergency-lishi"></span>
              <p>历史演练项目</p>
            </router-link>
          </li>
          <li @click="setProject()">
            <router-link to="">
              <span class="iconfont el-icon-emergency-shezhi"></span>
              <p>设置</p>
            </router-link>
          </li>
          <li @click="referenceMaterial()">
            <router-link to="">
              <span class="iconfont el-icon-emergency-ziliao"></span>
              <p>参考资料</p>
            </router-link>
          </li>
          <li @click="referenceAdmin()" v-if="type == 0">
            <router-link to="">
              <span class="iconfont el-icon-emergency-ziliao"></span>
              <p>部门管理</p>
            </router-link>
          </li>
          <li @click="referencePersonnel()" v-if="type == 0">
            <router-link to="">
              <span class="iconfont el-icon-emergency-ziliao"></span>
              <p>人员管理</p>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="contentRight">
      <div class="contentTop">
        <h1>我的参演：</h1>
        <ul class="clearfix">
          <li @click="">
            <span>我导演的演练项目:</span><i>{{myDirectorNum}}</i>
            <router-link to="/enterable" class="view">查看</router-link>
          </li>
          <li @click="">
            <span>我可参加的演练:</span><i>{{myJoinNum}}</i>
            <router-link to="/enterable" class="view">查看</router-link>
          </li>
          <li>
            <span>我参加过的历史演练:</span><i>{{myHisNum}}</i>
            <router-link to="/HistoricalRehearsal/1" class="view">查看</router-link>
          </li>
        </ul>
      </div>
      <div class="contentBottom">
        <!--<h1>系统公告:</h1>-->
        <!--<ul>-->
          <!--<li>-->
            <!--<span>-->
              <!--<a href="">市区幼儿园食品安全演练项目将于11月30日下午2点开始演练，请各参与单位准时参演练项目将于11月30日下午2点开始演练，请各参与单位准时参演</a>-->
            <!--</span>-->
            <!--<p>-->
              <!--<a href="">发布时间：2017/11/20  10:33</a>-->
            <!--</p>-->
          <!--</li>-->
          <!--<li>-->
            <!--<span>-->
               <!--<a href="">市区幼儿园食品安全演练项目将于11月30日下午2点开始演练，请各参与单位准时参演，请各参与单位准时参演练</a>-->
            <!--</span>-->
            <!--<p>-->
              <!--<a href="">发布时间：2017/11/20  10:33</a>-->
            <!--</p>-->
          <!--</li>-->
          <!--<li>-->
            <!--<span>-->
              <!--<a href="">市区幼儿园食品安全演练项目将于11月30日下午2点开始演练，请各参与单位准时参演，请各参与单位准时参演练</a>-->
            <!--</span>-->
            <!--<p>-->
              <!--<a href="">发布时间：2017/11/20  10:33</a>-->
            <!--</p>-->
          <!--</li>-->
        <!--</ul>-->
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        name: '',
        myDirectorNum: 0,//我导演的
        myJoinNum: 0,//我可参与演练项目的数目
        myHisNum: 0,//我参加过的历史演练
        myId: '',   //用户我的id
          type: '', //用户类型
      }
    },
    created() {
      this.$store.state.showHeader = '1';
      this.myId = JSON.parse(localStorage['user']).id;
      this.name = JSON.parse(localStorage.user).name;
      this.type = JSON.parse(localStorage.user).type;
//      console.log(this.type)
      this.getMyDirector();
      this.getMyHistory();
      this.getMyJoin();
    },
    methods: {
        //我导演的演练
      getMyDirector(){
          this.$axios.post('/api/director-my',{
              id: this.myId
          }).then((res)=>{
//              console.log(res)
              this.myDirectorNum = res.data.data.length;
          }).catch((err)=>{
          })
      },
      //获取我可参加的演练
      getMyJoin(){
        this.$axios.post('/api/design-me',{
          id: this.myId
        }).then((res)=> {
//            console.log(res);
            if(res.data.message!=='暂无数据'){
              let num = 0;
              let joinArr = [];
              for(let i in res.data.data){
	              if(res.data.data[i]!=res.data.data.score_user){
		              joinArr.push(res.data.data[i]);
	              }
              }

	            if(res.data.data.score_user.length != 0){//获取用户参与点评的演练
		            for(var i =0; i<res.data.data.score_user.length;i++){
			            joinArr.push(res.data.data.score_user[i]);
		            }
	            }
                for(var x in this.unique(joinArr)){
	            	if(this.unique(joinArr)[x].status !=2){
	            		num++;
                    }
                }
              this.myJoinNum = num;
            }
        }).catch((error)=> {
        	console.log(error)
        })
      },
      //获取我的历史演练
      getMyHistory(){
        this.$axios.post('/api/design-history-my',{
          id: this.myId
        }).then((res)=> {
          if(res.data.message!=='暂无数据'){
            this.myHisNum=res.data.data.length;
          }
        }).catch((error)=> {
        })
      },
      // 新建项目
      newProject(){
        this.$router.push('/newhome');
      },
      // 可参加的演练
      sureJoin(){
        this.$router.push('/enterable');
      },
      //可观看的
      toSee(){
        this.$router.push('/enterable');
        this.$store.state.status = true;
      },
      //历史演练
      toHistory(val){
          if(val == '1'){
              //我参与的
              this.$router.push('/HistoricalRehearsal/1');
          } else if(val == '2'){
              //全部
              this.$router.push('/HistoricalRehearsal/2');
          }
      },
      // 设置
      setProject(){
         this.$router.push('/set');
      },
      //参考资料
      referenceMaterial(){
      	if(this.type == 0){
			this.$router.push('/SuperReference');
        } else if(this.type == 1){
			this.$router.push('/Reference');
        }
      },
      //部分管理
      referenceAdmin(){
        this.$router.push('/Administrators');
      },
      //人员管理
      referencePersonnel(){
        this.$router.push('/Personnel')
      },
	    //去重
	    unique(array) {
		    var obj = {};
		    return array.filter(function(item, index, array){
			    //console.log(typeof item + JSON.stringify(item))
			    return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
		    })
	    },
    },
  }
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../../static/css/base.css';
  .homepage{
    width: 1280px;
    margin: 80px auto 0;
    .contentLeft{
      float: left;
      .contentTitle{
        margin-left: 70px;
        h1{
          font-family: PingFangSC;
          font-size: 32px;
          color: #706f70;
        }
      }
      .contentList{
        margin-left: 70px;
        width: 590px;
        height:290px;
        margin-top: 50px;
        ul{
          width: 100%;
          li{
            float: left;
            width: 29%;
            height:118px;
            a{
              text-align: center;
              margin: 0 auto;
              color: #4a4a4a;
              font-size: 18px;
              span{
                display:block;
                text-align: center;
                font-size: 50px;
                margin: 15px 0 13px 0;

              }
            }
          }
          li:nth-child(1){
            border-radius: 8px;
            background-color: #a1eaff;
            margin-bottom: 50px;
          }
          li:nth-child(2){
            margin-left: 35px;
            margin-right: 35px;
            background-color: #ddfbfd;
            border-radius: 8px;
            margin-bottom: 50px;
          }
          li:nth-child(3){
            background-color: #89cef5;
