            margin-bottom: 50px;
          }
          li:nth-child(4){
            background-color: #fff8e1;
            border-radius: 8px;
          }
          li:nth-child(5){
            background-color: #fafce7;
            margin-left: 36px;
            margin-right: 36px;
            border-radius: 8px;
          }
          li:nth-child(6){
            background-color: #f5fac4;
            border-radius: 8px;
          }
          li:nth-child(7){
            margin-right: 36px;
            margin-top: 50px;
            background-color: #d6efba;
            border-radius: 8px;
          }
          li:nth-child(8){
            margin-top: 50px;
            background-color: #e9d2ae;
            border-radius: 8px;
          }
        }
      }
    }
      //我的参演
    .contentRight{
      float: right;
      // margin-right: 220px;
      .contentTop{
        h1{
          font-family: PingFangSC;
          font-size: 32px;
          color: #706f70;
        }
        ul{
          margin-top: 14px;
          li{
            position: relative;
            height: 25px;
            font-family: PingFangSC;
            font-size: 18px;
            color: #706f70;
            width: 332px;
            border-bottom: solid 1px #bdbcbc;
            margin-top: 10px;
            padding-bottom: 9px;
            span{
              display: inline-block;
              width:180px;
              text-align: right;
            }
            a{
              display: inline-block;
              color: #4a90e2;
            }
            i{
              display: inline-block;
              width:100px;
              color: #f00;
              text-align: center;
            }
            .view{
              position: absolute;
              right: 0;
            }
          }
        }
      }
      .contentBottom{
        margin-top: 89px;
        h1{
          font-family: PingFangSC;
          font-size: 32px;
          color: #706f70;
          margin-bottom: 15px;
        }
        ul{
          li{
            span{
              a{
                overflow : hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                /* 这个是具体到到少行 */
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
            }
            width: 359px;
            font-family: PingFangSC;
            font-size: 16px;
            color: #706f70;
            margin-bottom: 19px;
            p{
              margin-top: 6px;
              a{
                font-family: PingFangSC;
                font-size: 12px;
                color: #706f70;
              }
            }
          }
        }
      }
    }
  }
</style>



